a.target-burger {
  margin-left: 20px;
  width: 42px;
  height: 42px;
  display: block;
  transition: 0.5s;
}
a.target-burger:hover {
  cursor: pointer;
  opacity: 0.45;
}
a.target-burger.burger--active ul.buns li.bun:not(:last-child) {
  width: 100%;
}
a.target-burger.burger--active ul.buns li.bun {
  background-color: #000;
  -webkit-transform: rotate(45deg) translateZ(0);
  transform: rotate(45deg) translateZ(0);
}
a.target-burger.burger--active ul.buns li.bun:last-child {
  -webkit-transform: rotate(-45deg) translateZ(0);
  transform: rotate(-45deg) translateZ(0);
}
a.target-burger ul.buns {
  width: 100%;
  height: 100%;
  list-style: none;
  margin: unset;
  padding: 0;
  -webkit-transition: -webkit-transform 1s cubic-bezier(0.23, 1, 0.32, 1), color 1s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), color 1s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  color: #fff;
}
a.target-burger ul.buns li.bun {
  width: 100%;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-3.75px) translateZ(0);
  transform: translateY(-3.75px) translateZ(0);
  -webkit-transition: -webkit-transform 1s cubic-bezier(0.23, 1, 0.32, 1), background-color 1s cubic-bezier(0.23, 1, 0.32, 1), width 1s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), background-color 1s cubic-bezier(0.23, 1, 0.32, 1), width 1s cubic-bezier(0.23, 1, 0.32, 1);
}
a.target-burger ul.buns li.bun:not(:last-child) {
  width: 25px;
}
a.target-burger ul.buns li.bun:last-child {
  bottom: 0;
  -webkit-transform: translateY(3.75px) translateZ(0);
  transform: translateY(3.75px) translateZ(0);
}

