.form__placeholder-container label {
  width: fit-content;
  color: #000;
  font-variant-numeric: lining-nums proportional-nums;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 110%;
  pointer-events: none;
  position: absolute;
  transition: all 200ms;
  top: 14px;
  left: 15px;
}

@media (min-width: 576px) {
  .form__placeholder-container label {
    top: 19px;
    left: 25px;
  }
}
.wpcf7-spinner {
  display: none;
}

.form__placeholder-container label span {
  font-size: 0.75rem;
  color: #A4A4A4;
}

.form__placeholder-container input:focus + label,
.form__placeholder-container input:not(:placeholder-shown) + label {
  top: -20px;
  left: 0;
  color: #FFF;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 110%;
  letter-spacing: -0.14px;
}

.form__placeholder-container textarea:focus + label,
.form__placeholder-container textarea:not(:placeholder-shown) + label {
  top: -20px;
  left: 0;
}

.form__checkbox {
  height: fit-content;
  position: relative;
}

.form__checkbox label {
  cursor: pointer;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 110%;
  color: #9FA2A7;
  position: relative;
  padding-left: 33px;
  z-index: 2;
}

.form__checkbox label a {
  color: #6f42c1;
  transition: 0.4s;
}

.form__checkbox label a:hover {
  text-decoration: underline;
}

.form__checkbox label::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0;
  top: -1px;
  border: 1px solid #9FA2A7;
  border-radius: 4px;
  transition: 0.4s ease-in;
}

.form__checkbox label::after {
  content: "";
  position: absolute;
  width: 8px;
  left: -2px;
  transform: rotate(90deg);
  top: 1px;
  height: 5px;
  border-left: 1px solid transparent;
  border-bottom: 1px solid transparent;
  transition: 0.2s ease-in;
  z-index: -1;
}

.form__checkbox input {
  display: none;
}

.form__checkbox input:checked ~ label::after {
  transform: rotate(-45deg);
  left: 6px;
  top: 5px;
  z-index: 1;
  border-left: 2px solid #9FA2A7;
  border-bottom: 2px solid #9FA2A7;
}